import Box from "./Box";

const Row = ({ rowData, rowNum, circle, topColorRed }) => {
  return (
    <div className="row">
      {rowData.map((el, boxNum) => (
        <Box
          rowNum={rowNum}
          boxData={el}
          boxNum={boxNum}
          circle={circle}
          topColorRed={topColorRed}
        />
      ))}
    </div>
  );
};

export default Row;
